import { useEffect, useState } from 'react';
import { deleteProduct, getProducts, searchProduct } from '../../service/admin';
import {
  Backdrop,
  Box,
  Card,
  CardActions,
  CardContent,
  CircularProgress,
  Grid,
  IconButton,
  InputBase,
  Typography,
  styled,
  Paper,
} from '@mui/material';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import SearchIcon from '@mui/icons-material/Search';
import QuizIcon from '@mui/icons-material/Quiz';

const Img = styled('img')({
  margin: 'auto',
  display: 'block',
  maxWidth: '100%',
  maxHeight: '100%',
  height: '250px',
  objectFit: 'cover',
});

export default function AdminDashboard() {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [keyword, setKeyword] = useState('');
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const fetchProducts = async () => {
    setLoading(true);
    try {
      const response = await getProducts();
      if (response.status === 200) setProducts(response.data);
    } catch (error) {
      console.log(error.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchProducts();
  }, []);

  const handleSearchProduct = async (e) => {
    setLoading(true);
    const keyword = e.target.value;
    setKeyword(keyword);
    try {
      const response = await searchProduct(keyword);
      if (response.status === 200) setProducts(response.data);
    } catch (error) {
      console.log(error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteProduct = async (productId) => {
    setLoading(true);
    try {
      await deleteProduct(productId);
      enqueueSnackbar('Product deleted successfully!', {
        variant: 'success',
        autoHideDuration: 5000,
      });
      fetchProducts();
    } catch (error) {
      enqueueSnackbar('Error deleting product!', {
        variant: 'error',
        autoHideDuration: 5000,
      });
    } finally {
      setLoading(false);
    }
  };

  const handleUpdateProduct = async (productId) => {
    navigate(`/admin/product/${productId}/edit`);
  };

  const handlePostFAQ = async (productId) => {
    navigate(`/admin/faq/${productId}/post`);
  };

  return (
    <>
      <Grid
        sx={{
          marginTop: 3,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <Paper
          component="form"
          sx={{
            p: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            width: 400,
          }}
        >
          <InputBase
            sx={{ m1: 1, flex: 1 }}
            placeholder="Enter keyword to search products"
            inputProps={{ 'aira-label': 'Enter keyword to search products' }}
            value={keyword}
            onChange={handleSearchProduct}
          />
          <IconButton type="button" sx={{ p: '10px' }} aria-label="search">
            <SearchIcon />
          </IconButton>
        </Paper>
      </Grid>
      <Box sx={{ width: '100%', mt: 3 }}>
        <Grid container rowSpacing={1} columnSpacing={{ sm: 2, md: 3 }}>
          {products.map((product) => (
            <Grid key={product._id}>
              <Card
                sx={{
                  p: 2,
                  pargin: 'auto',
                  flexGrow: 1,
                  maxWidth: 300,
                  backgroundColor: (theme) =>
                    theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
                }}
              >
                <Img
                  alt="complex"
                  src={`data:image/jpeg;base64,${product.img}`}
                />
                <CardContent>
                  <Typography gutterBottom variant="h5" component="div">
                    <strong>{product.name}</strong>
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    <strong>{product.description}</strong>
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    <strong>{product.price}</strong>
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    <strong>{product.category.name}</strong>
                  </Typography>
                </CardContent>
                <CardActions>
                  <Grid container spacing={2} sx={{ cursor: 'pointer' }}>
                    <Grid>
                      <QuizIcon onClick={() => handlePostFAQ(product._id)} />
                    </Grid>
                    <Grid>
                      <EditIcon
                        sx={{ color: 'green' }}
                        onClick={() => handleUpdateProduct(product._id)}
                      />
                    </Grid>
                    <Grid>
                      <DeleteIcon
                        sx={{ color: 'red' }}
                        onClick={() => handleDeleteProduct(product._id)}
                      />
                    </Grid>
                  </Grid>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Box>
      <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={loading}
      >
        <CircularProgress color="success" />
      </Backdrop>
    </>
  );
}
